@media screen and (max-width: 1300px) {
    .widgets-row {
        display: block;
    }
    .main-container {
        margin-top: 80px;
    }
    .site-navigation {
        width: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 20;
    }
    .site-navigation ul {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: distribute;
            justify-content: space-around;
        width: 100%;
        margin: 0;
    }
    .dashboard-container {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    }
}

@media screen and (max-width: 1200px) {
    .widgets-row {
        -ms-flex-direction: column;
        flex-direction: column;
    }
    .widget,
    .chart-days {
        margin: 20px auto;
        width: 85%;
    }
    .chart-days {
        height: 350px;
    }
}

@media screen and (max-width: 930px) {
    .dashboard-container {
        height: calc(100% - 78px);
    }
    .toolbar>li {
        display: none;
    }
    .toolbar li:last-of-type {
        display: block;
        margin: 0;
    }
    .main-header {
        -ms-flex-pack: distribute;
            justify-content: space-around;
        padding: 5px;
    }
    .main-container {
        margin-top: 60px;
    }
    .site-navigation__item {
        padding: 10px;
    }
    .chart-sales__header-container {
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
        -ms-flex-direction: column;
        flex-direction: column;
    }
    .chart-sales__header,
    .styled-select-label {
        width: auto;
    }
    .styled-select-label {
        margin: 10px auto;
    }
    .DayPicker-Day,
    .DayPicker-Weekday {
        padding: .1rem;
    }
    .DayPicker-NavButton--next {
        right: 0;
    }
    .DayPicker-NavButton--prev {
        left: 0;
    }
}


